.vue","sourcesContent":["<template>\n  <div>\n    <ctbHead>\n      <router-link active-class=\"cur\" v-for=\"(item,index) in authList\" :key=\"index\" :to=\"{path:item.action,query:{id:curId}}\" >\n        {{item.name}}\n        <el-badge v-show='index==2 && $store.state.policyNumber>0' :value=\"$store.state.policyNumber\" :max='99' class=\"item\" >\n        </el-badge>\n      </router-link>\n    </ctbHead>\n    <div class=\"container\">\n      <div class=\"quote\">\n        <div class=\"quote-ele\"><i></i>邦保养会员车管家-发票管理</div>\n        <div class=\"quote-nav\">\n          <router-link v-for=\"(item,index) in threeAuthList\" :to=\"{path:item.action,query:{id:curId}}\" active-class=\"cur\" :key=\"index\"   >\n            <span>{{item.name}}</span>\n          </router-link>\n        </div>\n      </div>\n      <router-view />   \n    </div>\n\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\n   \n  import detailsDialog from \"@/components/Dialog/Dialog\";\n  import RegionDialog from \"@/components/Dialog/RegionDialog\"\n  export default {\n    data() {\n      return {\n        list: [],\n        token: window.sessionStorage.getItem('bbytoken'),\n        authList:'',\n        seCurId:'',\n        curId:'',\n        threeAuthList:[\n            {name:'申请列表',action:'Toexamine'},\n            {name:'通过列表',action:'adopt'},\n        ]\n      }\n    },\n    components:{\n      detailsDialog,\n      RegionDialog\n    },\n    mounted() {\n\n      var id = this.$route.query.id;\n      this.curId = id;\n      this.$axios.post('admin/Auth/erAuth', {\n        token: window.sessionStorage.getItem('bbytoken'),\n        id: id\n      })\n        .then(res => {\n          \n          if(res.data.code == 1) {\n            var arr = res.data.data;\n            for(var i = 0; i < arr.length; i++) {\n            \n//              if(arr[i].son) {\n                if(arr[i].name == '发票管理') {\n                  this.seCurId = arr[i].id;\n\n                }\n//                for(var j = 0; j < arr[i].son.length; j++) {\n//                  if(arr[i].action != arr[i].son[j].action) {\n//                    arr[i].action = arr[i].son[0].action;\n//                  }\n//                  if(arr[i].son[j].name == '申请列表' && arr[i].name == '物料配给') {\n//                    this.thCurId = arr[i].son[j].id;\n//                  }\n//                }\n//              }\n            }\n            this.authList = arr;\n          } else {\n            this.$alert(res.data.msg, '提示', {\n              type: 'error'\n            });\n          }\n        })\n        .catch(err => {\n          alert(err); \n        })\n    },\n    watch: {\n      index() {\n        this.init();\n      },\n      profit(newVal,oldVal){\n        //输出的时候最大是15\n        if(newVal>15){\n          this.profit=15\n        }\n      }\n    }\n  }\n</script>\n<style scoped>\n  .item{\n      border: none;\n  }\n  >>> .el-badge__content{\n          border: none;\n\n      }\n  .shuju {\n    margin-top: 16px;\n    font-size: 16px;\n    text-align: center;\n    color: #999;\n    overflow: hidden;\n  }\n\n  .shuju> ul> li {\n    width: 50%;\n    margin-bottom: 50px;\n  }\n\n  .shuju .son {\n    margin-left: 20px;\n    display: inline-block;\n    vertical-align: top;\n  }\n\n  .shuju .son li {\n    display: block;\n  }\n\n  .shuju .grandson {\n    margin-left: 20px;\n    vertical-align: top;\n    display: inline-block;\n  }\n\n  .shuju .grandson li {\n    display: block;\n    margin-bottom: 7px;\n  }\n  .details_content:after{\n    display: block;\n    content: \"\";\n    clear: both;\n  }\n  .details_content{\n    width: 100%;\n    display: flex;\n    align-items: center;\n    justify-content: space-around;\n\n    /*border-right: 1px dashed #04BBFC;*/\n  }\n  .details_content>div{\n    flex: 1;\n  }\n  .details_content>div:first-child{\n    text-align: center;\n  }\n  .details_content>div:last-child{\n    border-left: 1px dashed #04BBFC;\n  }\n\n  .right {\n    display: inline-block;\n    